// Import variables
@import "variables.scss";

// Import hljs styles
@import "hljs-tomorrow.css";

//Import bulma
@import "bulma/sass/utilities/_all.sass";
@import "bulma/sass/grid/columns.sass";
@import "bulma/sass/layout/section.sass";
@import "bulma/sass/elements/box.sass";

body {
  background: #F0F4F8;
}

.wrapper {
  margin-right: auto;
  margin-left: auto;
  max-width: 960px;
  padding: 20px;
}

#editor {
  box-sizing: border-box;
  width: 100%;
  height: 80vh;
  background-color: #3a3a3a;
  color: white;
  padding: 20px;
  border: 0px;
  box-shadow: 0 4px 6px hsla(0,0%,0%,.2);
}

#preview {
  box-sizing: border-box;
  padding: 20px;
  background: white;
  height: 80vh;
  box-shadow: 0 4px 6px hsla(0,0%,0%,.2);
}

.title {
  background-color: $primaryColor;
  display: flex;
  color: white;
  border-radius: 4px 4px 0 0;
  padding: 10px;
  justify-content: space-between;
}

.title h3 {
  margin-block-start: 0px;
  margin-block-end: 0px;
  font-size: 16px;
}

code {
    background-color: #ffe5e5;
    color: #ff3232;
    padding: .25vw .5vw;
    border-radius: 2.5px;
  }
  
pre code {
    display: inline-block;
    background-color: #3a3a3a;
    color: white;
    padding: .25vw .5vw;
}

//Icons

.primary {
  fill: $secondaryColor;
}

.secondary {
  fill: white;
}

.hide {
  display: none;
}